import React from 'react';
import './main.css';
import './sprint.css';
import './bootstrap-theme.min.css';
import Card from 'react-bootstrap/Card';
import Form from 'react-bootstrap/Form';
import Table from 'react-bootstrap/Table';
import YoutubeEmbed from "./YoutubeEmbed";
const urls = require('./urls');

class PlayerChartComponent extends React.PureComponent {
	
	state = {
		playerObj: [],
		player: ''
	}
	
	onPlayerSelected = e => {
		e.persist();
		this.setState({playerObj: [] });
		this.setState({player: e.target.value}, () => {
			this.loadPlayerData();
		});	
	}
	
	loadPlayerData = async() => {
		try {
			if (this.state.player !== '') {
				let response = await fetch(`${urls.PLAYERSURL}?name=${this.state.player}`);
				let playersJSON = await response.json();	
				this.setState({gameObj: [] });							
				if (playersJSON !== null || playersJSON !== '') {
					for (let i = 0; i < playersJSON.length; i++) {
						this.setState(previousState => {
							let playerObj = [...previousState.playerObj];
								playerObj.push({
									game: playersJSON[i].game,
									date: playersJSON[i].date,
									death: playersJSON[i].death,
									time: playersJSON[i].time,
									link: playersJSON[i].link,
									gImage: playersJSON[i].gImage
							})							
							return { playerObj };
						})					
					}
				}
			}
		} catch {
			console.log('oopsie woopsie');
		}
	}
	
	render() {
		const { playerObj } = this.state;
		
		return (
			<Card style={{ borderWidth: "2px", borderColor: "#4179fc", borderRadius: "20px", margin: "2% 10% 2% 10%", background: "#b2ddff" }} >
				<Card.Body>
					<Card.Title style={{ fontWeight: "bold", fontSize: "24px" }}>
						Players
					</Card.Title>
					<Form>
						<Form.Control as="select" onChange={this.onPlayerSelected} disabled={!this.props.gotData}>
							<option key="-1" value=""/>
							{this.props.playerList.map((playerName, index) => (
								<option key={index} value={playerName}>{playerName}</option>
							))}
						</Form.Control>
					</Form>
					<br />
					<Table>
						<tbody>
							<tr>
								<th style={{textAlign: "center", width: "33%"}}>Game</th>
								<th style={{textAlign: "center", width: "33%"}}>Information</th>
								<th style={{textAlign: "center", width: "33%"}}>Video</th>
							</tr>
							{playerObj.map((player, index) => (
								<tr key={index}>
									<th>
										<img alt="game" className="game" src={`data:image/jpeg;base64,${player.gImage}`}/>
										<br/>
										<h1 style={{textAlign: "center", fontSize: "15px" }}>{player.game}</h1>
									</th>
									<th style={{textAlign: "center"}}>
										<a style={{display: "block"}}>
											<br/>
											Date: {player.date}
											<br/>
											<br/>
											Death: {player.death}
											<br/>
											<br/>
											Time: {player.time}
										</a>
									</th>
									<th>
										<YoutubeEmbed embedID={player.link} />
										<br/>
									</th>
								</tr>
							))}
						</tbody>
					</Table>
				</Card.Body>
			</Card >
		);
	}
};

export default PlayerChartComponent;