import React from "react";
import PropTypes from "prop-types";

const YoutubeEmbed = ({ embedID }) => (
  <div className="video-responsive">
    <iframe
	  title="video"
      width="0"
      height="0"
      src={embedID}
      frameBorder="0"
	  scrolling="no"
	  autoplay="false"
    />
  </div>
);

YoutubeEmbed.propTypes = {
  embedID: PropTypes.string.isRequired
};

export default YoutubeEmbed;