import React from 'react';
import './main.css';
import './sprint.css';
import './bootstrap-theme.min.css';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';

const TopBar = props => {
	return (
		<Navbar id="topNav" bg="primary" variant="dark" expand="lg" sticky="top" style={{ borderBottomColor: "#4179fc", borderBottomWidth: "2px" }}>
			<Navbar.Brand href='.'>
				<img alt="title" src={`data:image/jpeg;base64,`}/>
			</Navbar.Brand>
			<Navbar.Toggle aria-controls="basic-navbar-nav" />
			<Navbar.Collapse id="basic-navbar-nav">
				<Nav className="mr-auto" onSelect={selectedKey => props.menuSelect(selectedKey)}>
					<Nav.Link eventKey="games">Games</Nav.Link>
					<Nav.Link eventKey="players">Players</Nav.Link>
					<Nav.Link eventKey="latest">Latest Clears</Nav.Link>
					<Nav.Link target="_blank"  rel="noreferrer" href="https://docs.google.com/spreadsheets/d/1sQgRpXKnI-3HKmNL_cesol9UttXWN7nVgVQkGfRX2yY/edit#gid=0">Spreadsheet</Nav.Link>
					<Nav.Link eventKey="about">About</Nav.Link>
					<Nav.Link eventKey="credits">Credits</Nav.Link>
				</Nav>
			</Navbar.Collapse>
		</Navbar>
	);
};

export default TopBar;