import React, {Suspense} from 'react';
import './main.css';
import './sprint.css';
import './bootstrap-theme.min.css';
import TopBar from './topbar';
import GameChart from './gameChart';
import PlayerChart from './playerChart';
const DateChart = React.lazy(() => import('./dateChart'));
const urls = require('./urls');

class dashboardComponent extends React.PureComponent {

    state = {
		showDashboard: false,
        showPlayers: false,
		showGames: false,
		showLatest: false,
		playerList: [],
		gotData: false
    };
	
	componentDidMount = async() => {
		this._isMounted = true;
		this.loadPlayerData();
	}
	
	loadPlayerData = async() => {
		try {
			let playerJSON = await fetch(urls.PLAYERLISTURL);
			let arr = await playerJSON.text();
			if (arr !== null || arr !== '') {
				this.setState({playerList: JSON.parse(arr).sort(function (a, b) {return a.localeCompare(b, 'en', {'sensitivity': 'base'})})});
			}
				
			if (this._isMounted) {
				this.setState({
					gotData: true
				});
			}			
		} catch (error) {
			this.setState({
				gotData: false
			});
		}
	}
	
	componentWillUnmount() {
		this._isMounted = false;
	}

     menuSelect = (selectedKey) => {
        if (selectedKey === "games") {
			this.setState({ showGames: true, showPlayers: false, showDashboard: false, showLatest: false});
        }
        else if (selectedKey === "players") {
			this.setState({ showGames: false, showPlayers: true, showDashboard: false, showLatest: false});
        }
		else if (selectedKey === "spreadsheet") {
			this.setState({ showGames: false, showPlayers: false, showDashboard: false, showLatest: false});
        }
		else if (selectedKey === "latest") {
			this.setState({ showGames: false, showPlayers: false, showDashboard: false, showLatest: true});
        }
		else {
			this.setState({ showGames: false, showPlayers: false, showDashboard: true, showLatest: false});
		}
    };
	
    render() {
        const { 
			showDashboard, showPlayers, playerList, showGames, showLatest, gotData
		} = this.state;
		
        return (
            <div id="app-root">
                <TopBar menuSelect={this.menuSelect}/>
				{
					showDashboard
				}
                {
                    showGames && (
                        <GameChart/>
                    )
                }
                {
                    showPlayers && (
                        <PlayerChart playerList={playerList} gotData={gotData}/>
                    )
                }
				{
                    showLatest && (
						<div>
							<Suspense fallback={<div>Loading ... </div>}>
								<DateChart/>
							</Suspense>
						</div>
                    )
                }
            </div >
        );
    }
}

export default dashboardComponent;